export default createComponent<HTMLVmVideoElement, VideoProps>(
  'vm-video',
  new Set(['willAttach','hasCustomTextManager','crossOrigin','preload','poster','controlsList','autoPiP','disablePiP','disableRemotePlayback','mediaTitle']),
);
  